.Viewer {
  width: 100%;
  overflow: auto;

  .Viewer__container {
    min-width: 550px;
    margin-bottom: 1rem;
    position: relative;
    width: 100%;
    height: 17rem;
    background-image: url(https://static.pipk.top/chat/public/3165624214738918.png);
    background-origin: border-box;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-position-y: bottom;

    .Viewer__detail {
      background-color: rgba(245, 245, 245, 0.23);
      flex: 0 0 40%;
      display: flex;
      flex-direction: row;
      padding: 1rem;
      box-sizing: border-box;
      width: 100%;
      align-items: center;

      .Viewer__image {
        width: 4rem;
        height: 4rem;
        border-radius: 1rem;
        margin-right: 1rem;
      }

      .Viewer__detail-list {
        display: inline-flex;
        flex-direction: column;

        .Viewer__detail-name {
          font-size: 20px;
        }
        .Viewer__detail-bio {
          font-size: 16px;
        }
      }
    }
  }
}