.HomePage {
  overflow-y: auto;
  max-width: 980px;
  width: calc(100vw - 50px);
  background-color: #fff;
  padding: 20px;
  min-height: 100%;
  flex: 1;
  box-sizing: border-box;

  .HomePage-background {
    width: 100%;
    overflow: auto;

    .HomePage-container {
      min-width: 550px;
      margin-bottom: 1rem;
      position: relative;
      width: 100%;
      height: 17rem;
      background-image: url(https://static.pipk.top/chat/public/3165624214738918.png);
      background-origin: border-box;
      background-repeat: no-repeat;
      background-size: cover;
      border-radius: 0.5rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      background-position-y: bottom;

      .HomePage-detail {
        border: 0;
        margin: 0;
        padding: 0;
        flex: 0 0 60%;
        font-size: 30px;
        display: inline-flex;
        align-items: center;
        background-color: rgba(245, 245, 245, 0.23);
        flex: 0 0 100px;
        display: flex;
        flex-direction: row;
        padding: 1rem;
        box-sizing: border-box;
        width: 100%;
        // align-items: center;

        img {
          width: 4rem;
          height: 4rem;
          border-radius: 1rem;
          margin-right: 1rem;
        }

        .HomePage-detail__list {
          display: inline-flex;
          flex-direction: column;

          .HomePage-detail__name,
          .HomePage-detail__bio {
            font-size: 20px;
          }
        }
      }
    }
  }

  .HomePage-markdown__body {
    p:first-child {
      display: inline-flex;
      flex-direction: row;
      flex-wrap: wrap;

      a {
        flex: 0 0 auto;
      }

      &>img {
        flex: 0 0 90px;
      }
    }

    p {
      margin: 0;
      display: inline-flex;
      flex-direction: row;
      align-items: flex-start;
      text-align: left;

      a {
        margin-right: 0.3em;
      }
    }
  }
}