a {
  text-decoration: none; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Reem Kufi', 'Roboto', sans-serif;
  cursor: url(data:image/png;base64,), auto;
  width: 100%;
  min-height: 100vh; }

img {
  display: inline-block;
  max-width: 100%; }

a {
  color: var(--lightBlue7);
  display: inline-block;
  text-indent: 0;
  word-break: break-word; }

/* 设置滚动条的样式 */
::-webkit-scrollbar {
  width: 0px;
  height: 0px; }
  ::-webkit-scrollbar:end {
    display: none; }

/* 滚动槽 */
::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0); }

/* 滚动条滑块 */
::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 4px; }
  ::-webkit-scrollbar-thumb:end {
    display: none; }

::-webkit-scrollbar-thumb:window-inactive {
  background-color: rgba(0, 0, 0, 0.5); }

@font-face {
  font-family: "Reem Kufi";
  src: url("../ASSETS/font icons/fonts/a.woff"); }
